.loaderr{position:fixed;width:45px;height:45px;border-radius:50%;bottom:20px;right:-120px;z-index:2000;background-color:rgba(0,0,0,0.2)
}
.loaderr.show{right:20px
}
.loaderr .lds-spinner{color:#24C3A3;display:inline-block;position:absolute;width:80px;height:80px;top:50%;left:50%;-webkit-transform:translateX(-50%) translateY(-50%) scale(0.4);transform:translateX(-50%) translateY(-50%) scale(0.4)
}
.loaderr .lds-spinner div{-webkit-transform-origin:40px 40px;transform-origin:40px 40px;-webkit-animation:lds-spinner 1.2s linear infinite;animation:lds-spinner 1.2s linear infinite
}
.loaderr .lds-spinner div:after{content:" ";display:block;position:absolute;top:3px;left:37px;width:6px;height:18px;border-radius:20%;background:#fff
}
.loaderr .lds-spinner div:nth-child(1){-webkit-transform:rotate(0deg);transform:rotate(0deg);-webkit-animation-delay:-1.1s;animation-delay:-1.1s
}
.loaderr .lds-spinner div:nth-child(2){-webkit-transform:rotate(30deg);transform:rotate(30deg);-webkit-animation-delay:-1s;animation-delay:-1s
}
.loaderr .lds-spinner div:nth-child(3){-webkit-transform:rotate(60deg);transform:rotate(60deg);-webkit-animation-delay:-0.9s;animation-delay:-0.9s
}
.loaderr .lds-spinner div:nth-child(4){-webkit-transform:rotate(90deg);transform:rotate(90deg);-webkit-animation-delay:-0.8s;animation-delay:-0.8s
}
.loaderr .lds-spinner div:nth-child(5){-webkit-transform:rotate(120deg);transform:rotate(120deg);-webkit-animation-delay:-0.7s;animation-delay:-0.7s
}
.loaderr .lds-spinner div:nth-child(6){-webkit-transform:rotate(150deg);transform:rotate(150deg);-webkit-animation-delay:-0.6s;animation-delay:-0.6s
}
.loaderr .lds-spinner div:nth-child(7){-webkit-transform:rotate(180deg);transform:rotate(180deg);-webkit-animation-delay:-0.5s;animation-delay:-0.5s
}
.loaderr .lds-spinner div:nth-child(8){-webkit-transform:rotate(210deg);transform:rotate(210deg);-webkit-animation-delay:-0.4s;animation-delay:-0.4s
}
.loaderr .lds-spinner div:nth-child(9){-webkit-transform:rotate(240deg);transform:rotate(240deg);-webkit-animation-delay:-0.3s;animation-delay:-0.3s
}
.loaderr .lds-spinner div:nth-child(10){-webkit-transform:rotate(270deg);transform:rotate(270deg);-webkit-animation-delay:-0.2s;animation-delay:-0.2s
}
.loaderr .lds-spinner div:nth-child(11){-webkit-transform:rotate(300deg);transform:rotate(300deg);-webkit-animation-delay:-0.1s;animation-delay:-0.1s
}
.loaderr .lds-spinner div:nth-child(12){-webkit-transform:rotate(330deg);transform:rotate(330deg);-webkit-animation-delay:0s;animation-delay:0s
}
@-webkit-keyframes lds-spinner{
0%{opacity:1
}
100%{opacity:0
}
}
@keyframes lds-spinner{
0%{opacity:1
}
100%{opacity:0
}
